/* Icon Page */

@use "sass:math";

.search {
	position: relative;
	height: auto;
	width: 100%;
	float: left;
	margin-bottom: 10px;
	@include clearfix();

	svg {
		position: absolute;
		top: 0;
		left: 0;
		width: 50px;
		height: 50px;
		fill: $light-grey;
	}

	input {
		width: 100%;
		height: 50px;
		padding: 0 10px 0 50px;
		font-size: 25px;
		background: $white;
		border: 1px solid $light-grey;
		@include border-radius-all(4px);

		&:focus {
			outline: none;
			border-color: $cyan;
			@include box-shadow(1px 1px 2px rgba($off-black, .2));
		}
	}
}

.clear-search {
	display: none;
	background: none;
	border: none;
	position: absolute;
	top: 10px;
	right: 10px;
	width: 30px;
	height: 30px;
	border-radius: 40px;
	border: 1px solid transparent;

	svg {
		position: absolute;
		top: -6px;
		left: -6px;
		width: 40px;
		height: 40px;
	}

	&:hover,
	&:focus {
		outline: none;
		background: $off-white;
		border-color: $light-grey;

		svg { fill: $off-black; }
	}

	&.has-text { display: block; }
}

.code {
	width: 100%;
	float: left;
	margin-bottom: 30px;
}

.icons-column {
	[class*='ico-'] {
		width: 100%;
		height: 100%;
		fill: $blue;
	}
}

.icon-wrapper {
	background: none;
	position: relative;
	width: math.div(1,5) + '%';
	height: 160px;
	padding-bottom: 40px;
	border: 5px solid $white;
	margin-bottom: 0;
	float: left;
	display: block;
	background: $white;

	.title {
		display: block;
		position: absolute;
		bottom: 0;
		left: 0;
		right: 0;
		height: 50px;
		padding: 0 5px;
		line-height: 20px;
		text-align: center;
		color: $mid-grey;
	}

	&:hover,
	&:focus {
		outline: none;
		cursor: cell;
		background: tint($body-background, 0.5);

		.title { color: $off-black; }
	}

  @include breakpoint(large) { width: math.percentage(math.div(1,4)); }
  @include breakpoint(medium) { width: math.percentage(math.div(1,3)); }
  @include breakpoint(small) {
		height: 120px;
		@include font-size(1.2);
		margin-bottom: 10px;
  }
}
